/**
 * Twilio Recording Status Webhook
 * POST /api/webhooks/twilio/recording-status
 *
 * Receives call recording information from Twilio when a recorded call completes:
 * - RecordingSid: Recording ID
 * - RecordingUrl: URL to download recording (MP3/WAV)
 * - RecordingDuration: Duration in seconds
 * - CallSid: Associated call ID
 *
 * Stores recording URL in collection_attempts for compliance and review
 */

import { NextRequest, NextResponse } from 'next/server';
import { db, Timestamp } from '@/lib/firebase';
import { verifyTwilioSignature } from '@/lib/twilio-verify';
import { logInfo, logError, logExternalApiCall } from '@/utils/logger';
import { transcribeAudio } from '@/lib/openai';

export const dynamic = 'force-dynamic';

export async function POST(req: NextRequest): Promise<NextResponse> {
  try {
    // 1. Verify Twilio signature
    const isValid = await verifyTwilioSignature(req);
    if (!isValid) {
      logError('Invalid Twilio signature on recording-status webhook');
      return NextResponse.json({ error: 'Invalid signature' }, { status: 401 });
    }

    // 2. Parse form data
    const formData = await req.formData();
    const recordingSid = formData.get('RecordingSid') as string;
    const recordingUrl = formData.get('RecordingUrl') as string;
    const recordingDuration = formData.get('RecordingDuration') as string;
    const callSid = formData.get('CallSid') as string;
    const recordingStatus = formData.get('RecordingStatus') as string;

    logInfo('Twilio recording status update', {
      recordingSid,
      callSid,
      recordingDuration,
      recordingStatus,
    });

    // 3. Find corresponding collection attempt
    const attemptsSnapshot = await db
      .collection('collection_attempts')
      .where('callSID', '==', callSid)
      .limit(1)
      .get();

    if (attemptsSnapshot.empty) {
      logError('Collection attempt not found for call SID', { callSid });
      // Still return 200 to prevent Twilio retries
      return NextResponse.json({ success: true });
    }

    const attemptDoc = attemptsSnapshot.docs[0];

    // 4. Update with recording info
    // Construct full recording URL with auth token for download
    const accountSid = process.env.TWILIO_ACCOUNT_SID;
    const fullRecordingUrl = `https://api.twilio.com${recordingUrl}.mp3`;

    await attemptDoc.ref.update({
      callRecordingUrl: fullRecordingUrl,
      callDuration: parseInt(recordingDuration || '0', 10),
      updatedAt: Timestamp.now(),
    });

    logInfo('Collection attempt updated with recording', {
      attemptId: attemptDoc.id,
      recordingUrl: fullRecordingUrl,
      duration: recordingDuration,
    });

    // 5. AUDIT TASK #3: Transcribe recording using OpenAI Whisper
    // Download recording from Twilio and transcribe asynchronously
    try {
      const transcribeStart = Date.now();

      // Download recording from Twilio (authenticated with Basic Auth)
      const authHeader = Buffer.from(
        `${process.env.TWILIO_ACCOUNT_SID}:${process.env.TWILIO_AUTH_TOKEN}`
      ).toString('base64');

      const recordingResponse = await fetch(fullRecordingUrl, {
        headers: {
          Authorization: `Basic ${authHeader}`,
        },
      });

      if (!recordingResponse.ok) {
        throw new Error(`Failed to download recording: ${recordingResponse.statusText}`);
      }

      // Convert response to buffer for Whisper API
      const arrayBuffer = await recordingResponse.arrayBuffer();
      const audioBuffer = Buffer.from(arrayBuffer);

      // Transcribe audio using OpenAI Whisper
      const transcription = await transcribeAudio(audioBuffer, `recording-${recordingSid}.mp3`);

      logExternalApiCall('OpenAI Whisper', 'transcribe', Date.now() - transcribeStart);

      // Update collection attempt with transcription
      await attemptDoc.ref.update({
        callTranscript: transcription,
        transcribedAt: Timestamp.now(),
      });

      logInfo('Call recording transcribed successfully', {
        attemptId: attemptDoc.id,
        transcriptionLength: transcription.length,
      });

    } catch (transcriptionError: any) {
      // Log error but don't fail the webhook - transcription is optional
      logError('Failed to transcribe recording', transcriptionError, {
        attemptId: attemptDoc.id,
        recordingSid,
      });
    }

    return NextResponse.json({ success: true });
  } catch (error: any) {
    logError('Twilio recording-status webhook error', error);
    // Return 200 to prevent Twilio from retrying
    return NextResponse.json({ success: true }, { status: 200 });
  }
}
